.sidebar-button-wrapper {
  background-size: 24px auto;
  background-repeat: no-repeat;
  padding-left: 32px;
  height: 24px;
  line-height: 23px;	
}

.header-container {
	background-color: #784eda;
	font-family: SpoqaHanSans;
	font-size: 16px;
	font-weight: bold;
	color: #fff;
	padding: 16px;
	text-align: center;
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 9999;
	.sidebar-button {
		width: 24px;
	}
	.left-button {
		position: absolute;
		left: 16px;
	}
	.title {

	}
}
.view-container {
	overflow: hidden;
  .child-container {
  	padding-top: 54px;
  	overflow: auto;
  }
}

.back-btn {
	position: fixed;
  top: 16px;
  left: 16px;
  color: #fff;
  font-size: 14px;
  img {
  	height: 24px;
  }
}

.register-btn {
	position: fixed;
  top: 16px;
  right: 16px;
  color: #fff;
  font-size: 14px;
  opacity: 0.5;
  &.on {
  	opacity: 1;
  }
  .search-img {
  	width: 24px;
  }
}

