.classlist-container {
	margin: 0 16px;
	.class-container {
		table {
			text-align: center;
			thead {
				tr {
					th {
						padding: 17px 0;
						border-bottom: 1px solid #666;
						font-size: 14px;
						font-weight: bold;
						color: #333;
						text-align: left;
						.downarrow-img,
						.uparrow-img {
							width: 8px;
							margin-left: 4px;
							margin-top: -4px;
						}
						.btn-group-sm.dropdown {
							.dropdown-toggle {
								padding: 0;
								background-color: transparent;
								border: none;
								color: #333;
								font-weight: bold;
							}
							.dropdown-menu {
								border-radius: 4px;
								box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.2);
								background-color: #ffffff;
								width: 26px;;
								font-size: 14px;
								font-weight: normal;
								color: #333;
								border: none;
								margin-top: 20px;
								.dropdown-item {
									padding-top: 6px;
									padding-bottom: 6px;
								}
							}
						}
					}
				}
			}
			tbody {
				tr {
					td {
						padding: 10px 0;
						border-bottom: 1px solid #e6e6e6;
						font-size: 12px;
						font-weight: normal;
						color: #333;
						text-align: left;
						.apply-button { 
							width: 48px;
							border-radius: 2px;
							padding: 4px 12px;
							color: #fff;
							font-size: 12px;
							&.on {
								background-color: #784eda;
							}
							&.complete {
								background-color: #333;	
							}
						}
					}
				}
			}
		}
	}
	.modal-container {
		z-index: 2;
		position: fixed;
		width: 80vw;
		left: 10vw;
		top: 30vh;
		max-height: 300px;
		background-color: #fff;
		border-radius: 4px;
  	box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.2);
  	text-align: center;
  	.body {
  		.title {
			  padding: 24px 0;
			  font-size: 14px;
			  color: #333;
			  font-weight: bold;
			}
  		.text {
				margin: 0 auto;
			  font-size: 14px;
			  color: #333;
			  padding-bottom: 40px;
			  border-bottom: 1px solid #e6e6e6;
  		}
  	}
  	.footer {
  		.btn {
				width: 50%;
				height: 56px;
				line-height: 56px;
				color: #333;
				font-size: 14px;
				padding: 0;
  			&.confirm {
  				font-weight: bold;
  				color: #784eda;
  			}
  		}
  	}
	}

	.bg-container {
		z-index: 1;
		background-color: #000;
		opacity: 0.5;
		width: 100vw;
		height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
	}
}
