.board-container {
	.board-list {
		background-color: #fff;
	}
	.detail-container {
  	margin-bottom: 8px;
  	background-color: #fff;
  }
  .board-card-link:last-child {
  	.board-card  {
    	border-bottom: none;
    }
  }
	.board-card {
		padding: 16px 0 24px;
		margin: 0 16px;
    border-bottom: 1px solid #eee;
    background-color: #fff;
    
		.board-title {
      font-size: 14px;
      color: #333333;
		}

		.board-info {
			.board-writer {
				display: inline-block;
        font-size: 13px;
        font-weight: bold;
        color: #666;
			}
			.write-date {
				display: inline-block;
				font-size: 12px;
				color: #999;
				margin-left: 8px;
			}
			.reply-count {
				display: inline-block;
				font-size: 12px;
				color: #999;
				margin-left: 8px;
				padding-left: 8px;
				border-left: 1px solid #999;
				line-height: 10px;
			}
		}
	}
	.detail-content {
		white-space: pre-line;
		padding: 20px 16px;
		font-size: 14px;
		color: #333;
	}
	.comment-container {
		.title {
			padding: 16px 16px 8px;
			background-color: #eee;
			font-weight: bold;
			color: #999;
			font-size: 13px;
		}
		.comment-body {
			padding: 0 16px;
			margin-bottom: 76px;
			.comment-card:last-child {
				.comment-td {
					border-bottom: none;
				}
			}
			.comment-td {
				padding: 14px 0 17px;
				border-bottom: 1px solid #eee;
				.writer-img {
					width: 40px;
					height: 40px;
					border-radius: 20px;
					background-color: #eee;
				}
				.comment-content {
					font-size: 14px;
					color: #333;
				}
				.comment-info {
					.comment-writer {
						display: inline-block;
						font-size: 13px;
						font-weight: bold;
						color: #666;
					}
					.write-date {
						display: inline-block;
						margin-left: 12px;
						font-size: 12px;
						color: #999;
					}
				}
			}
		}
	}
	.comment-inputbox {
		padding: 8px;
		background-color: #f2f2f2;
		position: fixed;
		bottom: 0;
		width: 100%;
		box-sizing: border-box;
		.comment-input {
			height: 44px;
			border: none;
			border-radius: 2px;
			background-color: #ffffff;
			font-size: 13px;
			padding: 12px 16px;
			width: calc(100% - 68px);
			margin-top: -2px;
		}
		.register-button {
			width: 60px;
		  height: 44px;
		  border-radius: 2px;
		  background-color: #784eda;
		  margin-left: 8px;
		  color: #fff;
		  font-size: 14px;
		  border: none;
		}
	}
}