.write-container {
	padding: 16px;
	background-color: #fff;
	.content-wrapper {
		padding-bottom: 16px;
		&:last-child {
			padding: 0;
		}
		.title {
			color: #999;
			font-size: 13px;
		  font-weight: bold;
			.main {
			  
			}
			.sub {
				font-size: 12px;
			  font-weight: normal;
			  margin-left: 10px;
			}
		}
		input {
			width: 100%;
			border-radius: 2px;
  		background-color: #f8f8f8;
  		border: none;
  		padding: 10px 16px;
  		line-height: 20px;
  		height: 40px;
  		&.tag {
  			color: #784eda;
  		}
		}
		textarea {
			width: 100%;
			border-radius: 2px;
  		background-color: #f8f8f8;
  		border: none;
  		padding: 10px 16px;
  		line-height: 20px;
		}
		.image-wrapper {
			.attach-wrapper {
				position: relative;
				display: inline-block;
		    width: 80px;
		    height: 80px;
		    background-color: #f8f8f8;
		    text-align: center;
		    margin-right: 10px;
		    margin-top: 10px;
		    vertical-align: top;
				.attach-img {
					max-width: 80px;
				  max-height: 80px;
				  width: auto;
				  height: auto;
				  border-radius: 2px;
				}
				.close-btn {
					position: absolute;
					right: 2px;
					top: 0;
					line-height: 1;
					font-weight: bold;
				}
			}
			.image-addbox {
				display: inline-block;
				width: 80px;
				height: 80px;
				border-radius: 2px;
				background-color: #f8f8f8;
				background-position: center;
				background-size: 24px;
				background-repeat: no-repeat;
				margin-top: 10px;
			}
		}
		.hide-button {
			display: none;
			visibility: hidden;
			height: 0;
			width: 0;
		}
	}
}