.report-container {
	.content {
		padding: 0 16px;
		.desc{
			padding: 32px 0;
			font-size: 14px;
		  font-weight: normal;
		  text-align: center;
		  color: #333;
		  .sub-desc {
		  	color: #999;
		  }
		}
		.input-title {
			padding: 16px 0 8px;
		  font-size: 13px;
		  font-weight: bold;
		  color: #999;
		}
		.input {
			width: 100%;
			padding: 13px 16px 15px;
		  border-radius: 2px;
		  background-color: #f8f8f8;
		  border: none;
		  height: initial;
		}
		.upload-btn {
			width: 100%;
		  border-radius: 2px;
		  border: 1px solid #784eda;
		  font-size: 14px;
		  color: #784eda;
		  padding: 13px 0 15px;
		  background-color: #fff;
		}
		.review-option {
			width: 100%;
			padding: 13px 16px 15px;
			border-radius: 2px;
		  background-color: #fff;
		  height: initial;
		  color: #784eda;
		  border: 1px solid #784eda;
		  margin-bottom: 8px;
		  &.on {
		  	background-color: #784eda;
		  	color: #fff;
		  	background-image: url('../images/myclass/path-2@2x.png');
				background-repeat: no-repeat;
				background-position: center right 20px;
				background-size: 16px;
		  }
		  &:last-child {
		  	margin: 0;
		  }
		}
		.etc-comment {
	  	width: 100%;
	  	padding: 13px 16px 15px;
	  	height: initial;
	  	border: none;
		  border-radius: 2px;
		  font-size: 14px;
		  color: #333;
		  background-color: #f8f8f8;
	  }
		.point {
			text-align: center;
			color: #333;
	    padding-bottom: 32px;
			.num {
				font-size: 34px;
			  font-weight: bold;
			}
			.unit {
				font-size: 14px;
			  font-weight: normal;
			  margin-left: 6px;
			}
		}
		.cost-container {
			.cost-wrapper {
				margin-bottom: 16px;
				.input-wrapper {
					margin-right: 16px;
			    display: inline-block;
			    width: calc(100% - 48px - 16px);
			    border-bottom: 1px solid #eee;
					.input-label {
						line-height: 48px;
					}
					.cost-input {
						height: 48px;
						float: right;
						text-align: right;
						appearance: none;
						-webkit-appearance: none;
						border: none;
						color: #333;
						padding: 0 8px 4px;
					}
					.input-unit {
						float: right;
				    height: 48px;
				    line-height: 48px;
				    font-size: 14px;
				    font-weight: normal;
				    text-align: right;
				    color: #999;
					}
				}
				.image-addbox {
					display: inline-block;
					width: 48px;
					height: 48px;
					border-radius: 2px;
					background-color: #f8f8f8;
					background-position: center;
					background-size: 16px;
					background-repeat: no-repeat;
					vertical-align: bottom;
					&.on {
						background-size: cover;
					}
				}
			}
		}
		.image-wrapper {
			.image-addbox {
				display: inline-block;
				width: calc(50vw - 24px);
				height: calc(50vw - 24px);
				border-radius: 2px;
				background-color: #f8f8f8;
				background-position: center;
				background-size: 24px;
				background-repeat: no-repeat;
				vertical-align: bottom;
				margin-bottom: 8px;
				&.on {
					background-size: cover;
				}
				&:nth-child(2n+1) {
					margin-right: 8px;
				}
			}
		}
		.rangeslider {
			box-shadow: none;
			height: 2px;
			border-radius: 2px;
			background-color: #e6e6e6;
			.rangeslider__handle {
				width: 16px;
		    height: 16px;
		    background-color: #784eda;
		    box-shadow: none;
		    border: none;
			  &:after {
			  	content: none;
			  }
			}
		}
		.rangeslider__fill {
			background-color: #784eda;	
		}
		.rangeslider__labels {
			.rangeslider__label-item {
				font-size: 14px;
				color: #999;
			}
		}
	}
}