.sidebar-container {
  width: 70vw;
  height: 100%;
  max-width: 280px;
  background-color: #fff;
  .sidebar-content {
    padding: 0 24px;
    height: 100%;
    background-color: #fff; 
    .sidebar-link {
      display: block;
      color: #757575;
      text-decoration: none;
      &.on .sidebar-button-wrapper .sidebar-link-title {
        color: #784eda;
        font-weight: bold;
      }
      .sidebar-button-wrapper {
        margin-top: 32px;
        .sidebar-link-title {
          width: 52px;
          height: 20px;
          font-size: 14px;
          color: #333;
        }
      }
    }
  }

  .userinfo-container {
    padding:  48px 24px;
    background-color: #784eda;
    .user-image {
      display: inline-block;
      width: 80px;
      height: 80px;
      box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.2);
      background-color: #ffffff;
      border-radius: 40px;
      background-size: contain;
      background-repeat: no-repeat;
    }
    .text-container {
      display: inline-block;
      vertical-align: top;
      height: 80px;
      margin-left: 24px;
      color: #fff;
      padding: 18px 0;
      box-sizing: border-box;
      .user-nickname {
        font-size: 16px;
        font-weight: bold;
      }
      .user-realname {
        color: #d3c0ff;        
        font-size: 14px;
     }
    }
  }
}
