.classdetail-container {
  padding-bottom: 60px;
	.class-container {
		background-color: #fff;
		.detail-header {
		  font-size: 13px;
		  font-weight: bold;
		  color: #999;
		  background-color: #efefef;
		  padding: 16px 16px 8px;
		  .detail-subheader {
				font-size: 12px;
				font-weight: normal;
				color: #999;
		  }
		}
		.detail-table {
			margin: 8px 16px;
			width: calc(100% - 32px);
			text-align: center;
			thead {
				tr {
					th {
						
					}
				}
			}
			tbody {
				tr {
					td {
						padding: 8px 0;
						font-size: 14px;
						text-align: left;
					  height: 20px;
					  font-weight: normal;
					  &.left-td {
					  	color: #999;
					  	width: 25%;
					  	vertical-align: top;
					  }
					  &.right-td {
					  	color: #333;
					  }
					}
				}
			}
		}
		.classlist-wrapper {
			padding-top: 8px;
			.class-wrapper {
				padding: 8px 16px 16px;
				font-size: 14px;
				color: #333;
				font-weight: normal;
				.school-name,
				.class-info {
					line-height: 1.4em;
				}
				.payment-btn {
					border-radius: 2px;
					border: 1px solid #eee;
					color: #999;
					font-size: 14px;
					font-weight: normal;
					padding: 13px 0 15px;
					width: 100%;
					margin-top: 15px;
					background-color: transparent;
					&.on {
						background-color: #784eda;
						color: #784eda;
					}
				}
			}
		}
		.report-list {
			margin: 0 16px;
			.report-item {
				padding: 18px 0;
				border-bottom: 1px solid #eee;
				.title {
					display: inline-block;
					font-size: 14px;
					color: #333;
					font-weight: normal;
				}
				.input-button {
					display: inline-block;
					float: right;
					font-size: 14px;
					color: #999;
					font-weight: normal;
					background-image: url('../images/myclass/btn-arrow-right@2x.png');
					background-repeat: no-repeat;
    			background-position: 40px;
    			background-size: 20px;
					padding-right: 20px;
				}
			}
		}
	}
	.divider {
		background-color: #eee;
		width: 100%;
		height: 16px;

	}
	.report-button {
		width: 100%;
		padding: 18px 0 19px;
		text-align: center;
		color: #fff;
		background-color: #784eda;
		position: fixed;
		bottom: 0;
	}
}

