		.detail-container {
			.detail-header { 
			}
			.detail-table {
				tbody {
					tr {
						td {
							font-size: 12px;
							.left-td {
						    width: 25%;
							}
							.right-td {
						    
							}
						}
					}
				}
			}
		}
	}
}