.payment-container {
	padding-bottom: 60px;
	.search-container {
		.month-option {
			padding: 16px;
			text-align: center;
			.month-button {
				display: inline-block;
				width: calc(33.3% - 8px);
				padding: 13px 0 15px;
				margin-right: 8px;
				text-align: center;
				border-radius: 2px;
				font-size: 14px;
				border: 1px solid #eee;
				color: #999;
				&:last-child {
					margin-right: 0;
				}
				&.on {
					border: 1px solid #784eda;
					color: #784eda;
				}
			}
		}
		.month-range {
			padding: 0 16px;
			.input-wrapper {
				display: inline-block;
				width: calc(50% - 32px);
				font-size: 14px;
				border-bottom: 1px solid #eee;
				padding-bottom: 2px;
				margin-bottom: 16px;
				.date-selector {
					width: 80%;
					color: #784eda;
					font-size: 14px;
					border: none;
				}
				.date-input {
					border: none;
					color: #784eda;
				}
				&:first-child {
					margin-right: 8px;
				}
			}
			.search-button {
				display: inline-block;
				background-image: url('../images/payment/search-btn@2x.png');
				background-repeat: no-repeat;
				background-size: 48px;
				width: 48px;
				height: 48px;
				vertical-align: bottom;
				margin-bottom: 16px;
		    margin-left: 8px;
			}
		}
	}
	.payment-wrapper {
		.detail-header {
		  font-size: 13px;
		  font-weight: bold;
		  color: #999;
		  background-color: #efefef;
		  padding: 16px 16px 8px;
		  .detail-subheader {
		  	float: right;
		  	display: inline-block;
				font-size: 12px;
				font-weight: normal;
				color: #999;
		  }
		}
		.summary-wrapper {
			padding: 8px 16px;
			table {
				tbody {
					tr {
						td {
							padding: 8px 0;
							font-size: 14px;
							color: #333;
							&.value {
								text-align: right;
							}
						}
						&.total {
							td {
								font-weight: bold;
							}
						}
					}
				}
			}
		}
		.select-wrapper {
			background-color: #fff;
			.select-header {
				.fee-button {
					display: inline-block;
					width: 50%;
					color: #999;
					border-bottom: 1px solid #eee;
					text-align: center;
					font-size: 14px;
					padding: 17px 0;
					&.on {
						font-weight: bold;
						color: #784eda;
						border-color: #784eda;
					}
				}
			}
			.fee-wrapper {
				padding: 20px 16px 0;
				font-size: 14px;
				color: 333;
				.inning-info {

				}
				.fee-info {
					margin-top: 16px;
					background-color: #faf9ff;
					padding: 16px 0;
					table {
						tbody {
							tr {
								td {
									font-size: 14px;
									font-weight: normal;
									color: #333;
									text-align: right;
									&.title {
										padding-left: 16px;
										text-align: left;
										font-size: 13px;
										color: #999;
										.sub {
											margin: 0;
									    position: absolute;
									    margin-top: -6px;
									    font-size: 13px;
										}
									}
									&.value {
										padding-right: 16px;
										font-weight: bold;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}


@media (max-width: 340px) {
	.payment-container {
		.search-container {
			.month-option {
				.month-button {
					font-size: 14px;
				}
			}
			.month-range {
				.input-wrapper {
					font-size: 12px;
					.date-selector {
						font-size: 12px;
					}
				}
			}
		}
	}
}