.notice-container {
	&.list {background-color: #eee;}
	.search-wrapper {
    position: fixed;
    height: 40px;
    border-radius: 2px;
    background-color: #ffffff;
    top: 8px;
    left: 8px;
    width: calc(100vw - 62px);
    .btn-group-sm.dropdown {
    	display: inline-block;
			.dropdown-toggle {
				height: 40px;
				// padding: 0 16px;
				width: 20vw;
				color: #784eda;
				font-size: 14px;
				text-align: center;
				background-color: transparent;
				border: none;
				font-weight: normal;
				padding: 0;
			}
			.dropdown-menu {
				border-radius: 4px;
				box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.2);
				background-color: #ffffff;
				width: 26px;;
				font-size: 14px;
				font-weight: normal;
				color: #333;
				border: none;
				margin-top: 12px;
				padding: 12px 0;
				.dropdown-item {
					padding-top: 12px;
					padding-bottom: 12px;
					&.on {
					  color: #784eda;
					  font-weight: bold;
					  background-image: url('../images/notice/ic-list-check@2x.png');
					  background-size: 16px;
					  background-repeat: no-repeat;
					  background-position: center right 20px;
					}
				}
			}
		}
		.search-input {
			width: calc(80vw - 62px);
			color: #333;
			font-size: 14px;
			border: none;
			padding-left: 0;
			padding-right: 0;
		}
	}
	.notice-list {
		background-color: #eee;
	}
	.fixed-container,
  .normal-container,
  .detail-container {
  	margin-bottom: 8px;
  	background-color: #fff;
  	a:last-child {
  		.board-card:last-child {
  			border-bottom: none;
  		}
  	}
  	.empty-result {
			width: 100%;
			text-align: center;
			color: #333;
			font-size: 14px;
			background-color: #eee;
  	}
		.board-card {
			padding: 16px 0 20px;
      border-bottom: 1px solid #eee;
      margin: 0 16px;
      background-color: #fff;
      .board-tag {
				display: inline-block;
	      height: 18px;
	      line-height: 18px;
	      border-radius: 2px;
	      background-color: #f6f2ff;
	      padding: 0 4px;
	      font-size: 12px;
	      color: #784eda;
			}

			.board-title {
        font-size: 14px;
        color: #333333;
			}

			.board-info {
				.board-writer {
					display: inline-block;
          font-size: 13px;
          font-weight: bold;
          color: #666;
				}
				.write-date {
					display: inline-block;
					font-size: 12px;
					color: #999;
					margin-left: 8px;
				}
				.reply-count {
					display: inline-block;
					font-size: 12px;
					color: #999;
					margin-left: 8px;
					padding-left: 8px;
					border-left: 1px solid #999;
					line-height: 10px;
				}
			}
		}
		.detail-content {
			white-space: pre-line;
			padding: 20px 16px;
			font-size: 14px;
			color: #333;
		}
		.comment-container {
			.title {
				padding: 16px 16px 8px;
				background-color: #eee;
				font-weight: bold;
				color: #999;
				font-size: 13px;
			}
			.comment-body {
				padding: 0 16px;
				margin-bottom: 76px;
				.comment-card:last-child {
					.comment-td {
						border-bottom: none;
					}
				}
				.comment-td {
					padding: 14px 0 17px;
					border-bottom: 1px solid #eee;
					.writer-img {
						width: 40px;
						height: 40px;
						border-radius: 20px;
						background-color: #eee;
					}
					.comment-content {
						font-size: 14px;
						color: #333;
					}
					.comment-info {
						.comment-writer {
							display: inline-block;
							font-size: 13px;
							font-weight: bold;
							color: #666;
						}
						.write-date {
							display: inline-block;
							margin-left: 12px;
							font-size: 12px;
							color: #999;
						}
					}
				}
			}
		}
		.comment-inputbox {
			padding: 8px;
			background-color: #f2f2f2;
			position: fixed;
			bottom: 0;
			width: 100%;
			box-sizing: border-box;
			.comment-input {
				height: 44px;
				border: none;
				border-radius: 2px;
				background-color: #ffffff;
				font-size: 13px;
				padding: 12px 16px;
				width: calc(100% - 68px);
				margin-top: -2px;
			}
			.register-button {
				width: 60px;
			  height: 44px;
			  border-radius: 2px;
			  background-color: #784eda;
			  margin-left: 8px;
			  color: #fff;
			  font-size: 14px;
			  border: none;
			}
		}
	}
}